let firstDay='2023-01-19'
dayjs.extend(calendar)
dayjs.locale('zh-cn');
const getListData = (value) => {
  let listData;
  let year= value.year()
  let month=value.month()+1
  let date= value.date()
  let newdate=year+'-'+month+'-'+date
  let distance = dayjs(newdate).diff(firstDay, 'day', true)
  const day=5
  const condition=2
  const conditionless=3
  let bb="白班"
  let yb="夜班"
  console.log(distance,newdate,value.calendar(dayjs('2008-01-01')))


  let nl=new Date(newdate).toLocaleString('zh-CN-u-ca-chinese')
      .replace(/(\d+)\s*?年/, (_,y)=>"甲乙丙丁戊己庚辛壬癸".charAt((y-4)%10)
          + "子丑寅卯辰巳午未申酉戌亥".charAt((y-4)%12))
  nl=nl.slice(0,6)
    nl=nl.slice(2,6)
  if(distance>0) {
    const num=distance
    let numday = Math.floor(num % day)
    if(numday==0){
      listData = [
        {
          type: 'warning',
          content: nl+' '+bb,
        }
      ];
    }else if(numday==1){
       listData = [
        {
          type: 'warning',
          content: nl+' '+yb,
        }

      ];
    }else{
       listData = [
        {
          type: 'success',
          content: nl+' '+'休息',
        }
      ];
    }

  }else{
    const num=Math.abs(distance)
    let numday = Math.floor(num % day)

    if(numday==4){
       listData = [
        {
          type: 'warning',
          content: nl+' '+ bb,
        }

      ];
    }else if(numday==3){
       listData = [
        {
          type: 'warning',
          content: nl +' '+yb,
        }

      ];
    }else{
       listData = [  {
        type: 'success',
        content: nl+' '+'休息',
      }]
    }

  }

  return listData || [];
};
